body {
  margin: 0;
  /*padding: 4px;*/
  font-family: sans-serif;
}

.shadowed{
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 5px 15px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 5px 15px 0 rgba(0, 0, 0, 0.15);
}

.form-container{
  margin-top: 30px;
  padding: 30px;
  /*background: rgba(255,255,255,1);*/
  background: #f8f8f8;
}

.form-container footer{
   margin-top: 3%;
}

.navbar-default {
    /*background-color: #3a3a3a;*/
    border-color: #e7e7e7;
    border-bottom-color: transparent;
    border-radius: 0;
    padding-top: 5px;
    padding-bottom: 5px;
}


.navbar-default .navbar-nav>li>a {
    font-size: medium;
    font-weight: bold;
    color: #3d90b2;
}

.navbar-brand{
    font-weight: bold;
}

